import Vue from 'vue';
import Router from 'vue-router';
import Main from './components/Main.vue';
import Education from './components/pages/Education.vue';
import Experience from './components/pages/Experience.vue';
import Projects from './components/pages/Projects.vue';
import Research from './components/pages/Research.vue';
import About from './components/pages/About.vue';
import Connect from './components/pages/Connect.vue';
import Skills from './components/pages/Skills.vue';
import Awards from './components/pages/Awards.vue';
import SandBox from './components/pages/SandBox.vue';


Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: Main,
    },
    {
        path: '/education',
        name: 'education',
        component: Education,
    },
    {
        path: '/experience',
        name: 'experience',
        component: Experience,
    },
    {
        path: '/projects',
        name: 'projects',
        component: Projects,
    },
    {
        path: '/research',
        name: 'research',
        component: Research,
    },
    {
        path: '/about',
        name: 'about',
        component: About,
    },
    {
        path: '/connect',
        name: 'connect',
        component: Connect,
    },
    {
        path: '/skills',
        name: 'skills',
        component: Skills,
    },
    {
        path: '/awards',
        name: 'awards',
        component: Awards,
    },
    {
        path: '/sandbox:-)',
        name: 'sandbox',
        component: SandBox,
    }
  ],
});
