<template>
    <div>
        <div v-scroll-reveal.reset :style="`background-color:${rand}; min-height:${randHeight}`" class="column"></div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            rand: '#'+(Math.random()*0xFFFFFF<<0).toString(16),
            randHeight: (Math.round(Math.random()*400+100)).toString() + 'px',
        }
    },
    props: {
        title: String,
        color: {
            Type: String,
            default: 'white',
        },
        background: {
            Type: String,
            default: '#5BA3EF',
        },
        fontsize: {
            Type: String,
            default: '',
        }
    }
}
</script>

<style scoped>
.column {
  min-width: calc(90%);
  background-color: black;
  margin: 10px;
  border-radius: 20px;
}
</style>